const { Meta } = Card
function Smatching (props) {
  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
    borderRadius: '20px'
  }
  // 模态框打开关闭状态
  const [open1, setOpen1] = React.useState(false)
  const handleOpen1 = () => setOpen1(true)
  const handleClose1 = () => setOpen1(false)
  // 年份
  const [yeardata, setYeardata] = useState(2023)
  // localStorage.setItem('area', JSON.stringify({
  //   subject: 0,
  //   score: 555
  // }))
  // 获取数据
  const [matchdata, setMatchdata] = useState([])
  const [visible, setVisible] = useState(false)
  const [open, setOpen] = useState(false)
  // 是否登录
  const [isLogin, setIsLogin] = useState(false)
  useEffect(() => {
    if (localStorage.getItem('token')) {
      setIsLogin(true)
    }
  }, [])
  const showDrawer = () => {
    setOpen(true)
  }
  const onClose = () => {
    setOpen(false)
    setVisible(false)
  }
  // 提交表单且数据验证成功后回调事件
  const onFinish = (values) => {
    // 检查是否已登录
    if (!isLogin) {
      handleOpen1()
      message.error('请先登录')
      return
    }

    console.log('Success:', values)
    // 存储到本地
    values.score = parseInt(values.score, 10)
    localStorage.setItem('area', JSON.stringify(values))
    async function getmatchdata () {
      try {
        const response = axios.get(`http://47.237.100.43:8081/scoreLine/match?subject=${values.subject}&totalScore=${values.score}`)
        response.then((result) => {
          setMatchdata(result.data.data)
        })
      } catch (error) {
        console.log(error)
      }
    }
    getmatchdata()
    onClose()
  }
  // 提交表单且数据验证失败后回调事件
  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo)
  }
  const year2021 = matchdata.filter(item => item.year === 2021)
  const year2022 = matchdata.filter(item => item.year === 2022)
  const year2023 = matchdata.filter(item => item.year === 2023)
  // table配置
  const columns = [
    {
      title: '招生院校',
      dataIndex: 'schoolName',
      key: 'schoolName'
    },
    {
      title: '学校id',
      dataIndex: 'id',
      key: 'id'
    },
    {
      title: '分数线',
      dataIndex: 'totalScore',
      key: 'totalScore'
    },
    {
      title: '批次',
      dataIndex: 'batch',
      key: 'batch'
    },
    {
      render: (text, record) => <Button type="primary" onClick={() => {
        sessionStorage.setItem('schoolId', JSON.stringify(record))

        props.history.push('/collegeview/schoolprofile')
      }}>查看院校详情</Button>
    }
  ]
  // 当进入页面时，删除schoolId
  useEffect(() => {
    sessionStorage.removeItem('schoolId')
  }, [])
  return (
    <div>
      <Businesspage name="智能匹配"></Businesspage>
      <Modal
              open={open1}
              onClose={handleClose1}
              aria-labelledby="modal-modal-title"
              aria-describedby="modal-modal-description"
            >
              <Box sx={style}>
                <h2 className='Registerselect-Modal-h2' >
                  提示
                </h2>
                <div>此功能需要登录后才能使用,点击按钮跳转到登录页面
                </div>
                <div style={{ padding: '2vh 5vh' }}>
                  <Button size='large' style={{ float: 'left', marginLeft: '2vh' }} onClick={() => {
                    handleClose1()
                  }}>取消</Button>
                  <Button type='primary'style={{ backgroundColor: 'rgb(255, 156, 14)', marginRight: '2vh', float: 'right' }} size='large' onClick={() => {
                    props.history.push('/login')
                  }}>确定</Button>
                </div>
              </Box>
        </Modal>
      <div>
        <div>
          <div className="Tipofftelephone-nav">
            <div style={{ fontSize: '5vh' }}>找到最适合你的专业!</div>
            <Alert
              banner
              message={
                <Marquee pauseOnHover gradient={false}>
                  1、本查询系统的信息仅供参考，具体数据请以学校官网或考试院公布为准。
                  2、本站数据未经授权严禁转载，违者将依法追究责任。
                  3、如对本系统或相关服务有任何疑问，可发送邮件至：lijin@eol.cn，或拨打电话400-0678-211，也可点击【意见反馈】
                  反馈于我们。
                </Marquee>
              }
            />
          </div>
          <div className="Smatching-data">
            <ConfigProvider
              theme={{
                token: {
                  colorPrimary: 'rgb(255, 144, 16)'
                }
              }}
            >
              <Card
                style={{ width: 300 }}
                hoverable
                onClick={showDrawer}
                extra={
                  <a
                    onClick={() => {
                      showDrawer()
                    }}
                  >
                    修改成绩
                  </a>
                }
              >
                <Meta
                  title={'当前成绩'}
                  description={
                    <div style={{ display: 'flex', flexDirection: 'column' }}>
                      <span>
                        科目：
                        {
                        JSON.parse(localStorage.getItem('area')).subject === 0 ? '文科' : '理科'
                        }
                      </span>
                      <span>
                        分数：{JSON.parse(localStorage.getItem('area')).score}
                      </span>
                    </div>
                  }
                />
              </Card>
            </ConfigProvider>
            <Drawer
              title="请填写您的高考信息"
              placement="left"
              onClose={onClose}
              open={open}
              visible={visible}
            >
              <Form
                name="basic"
                labelCol={{
                  span: 8
                }}
                wrapperCol={{
                  span: 16
                }}
                style={{
                  maxWidth: 600
                }}
                initialValues={{
                  remember: true
                }}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                autoComplete="off"
              >
                <Form.Item
                  label="预估分数"
                  name="score"
                  rules={[
                    {
                      required: true,
                      message: '请输入你的预估分数!'
                    },
                    {
                      validator: (_, value) => {
                        if (value >= 0 && value <= 750) {
                          return Promise.resolve()
                        }
                        return Promise.reject(
                          new Error('分数必须在0到750之间')
                        )
                      }
                    },
                    {
                      pattern: /^\d+$/,
                      message: '只能输入数字'
                    }
                  ]}
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  label="高考科目"
                  name="subject"
                  rules={[
                    {
                      required: true,
                      message: '请选择你的高考科目!'
                    }
                  ]}
                >
                  <Select
                    placeholder="请选择你的高考科目!"
                    allowClear
                    options={[
                      { value: 1, label: '理科' },
                      { value: 0, label: '文科' }
                    ]}
                  ></Select>
                </Form.Item>
                <Form.Item
                  wrapperCol={{
                    offset: 8,
                    span: 16
                  }}
                >
                  <Button type="primary" htmlType="submit">
                    提交
                  </Button>
                </Form.Item>
              </Form>
            </Drawer>
            <div></div>
          </div>
        </div>
        <div style={{ margin: '2vh 12vw' }}>
          <Button onClick={() => { setYeardata(2021) }}>2021</Button>
          <Button onClick={() => { setYeardata(2022) }}>2022</Button>
          <Button onClick={() => { setYeardata(2023) }}>2023</Button>
          {yeardata === 2021 && <Table dataSource={year2021} columns={columns}/>}
          {yeardata === 2022 && <Table dataSource={year2022} columns={columns}/>}
          {yeardata === 2023 && <Table dataSource={year2023} columns={columns}/>}
        </div>
      </div>
      <Footcommon></Footcommon>
    </div>
  )
}
Smatching.propTypes = {
  history: PropTypes.object.isRequired,
  location: PropTypes.object.isRequired,
  match: PropTypes.object.isRequired
}
export default withRouter(Smatching)
